.products{
  padding-top: 121px;
  padding-bottom: 98px;
  background-color: var(--text-color-white);
}
.products_container{

 
  @media screen and (min-width: 1280px) {
    padding: 0 95px;
  }
}

.product_title {
  width: 100%;
  margin: auto;
  @media screen and (min-width: 768px) {
    width: 283px;
  }
  @media screen  and (min-width: 1200px) {
    width: 350px;
  }
}

.subtitle {
  margin: 0;
  padding-bottom: 15px;
  font-family: Titan One;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
  color: var(--circle-color);
}

.title {
  margin: 0;
  padding-bottom: 31px;
  font-family: Titan One;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--title-color);
}

.products_list {
   @media screen  and (max-width: 767px) {
    display: block;
   }
  
    @media screen and (min-width: 768px)  {
      display: flex;
      justify-content: center;
      // width: 700px;

    }
  
    @media screen and (min-width: 1280px) {
      display: flex;
      // width: 1054px;
      justify-content: center;
    }
  }
  
  .products_list__item {
    position: relative;
    display: block;
    // width: 280px;
    width: 100%;
    height: 598px;
    margin-bottom: 0;
    text-align: center;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    transform: scale(1);
    &:hover {
      transform: translateY(-10px);
      transform: scale(1.1);
    }
  
    @media screen  and (max-width: 767px) {
      margin-top: 0;

      &:not(:last-child) {
        margin-bottom: 17px;
      }
    }
  
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      width: calc((100% - 20px) / 3);
      height: 589px;
  
      &__item:not(:last-child) {
        margin-right: 20px;
      }
    }
  
    @media screen and (min-width: 1280px) {
      width: calc((100% - 30px) / 3);
      height: 735px;
      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }
  
  .products_image {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    z-index: 2;
    display: block;
    max-width: 100%;
    margin: 0;
  
    @media screen and (min-width: 320px) and (max-width: 767px) {
      width: 249px;
    }
  
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      width: 220px;
    }
  
    @media screen and (min-width: 1280px) {
      width: 335px;
    }
  }
  
  .products_content {
    position: absolute;
    bottom: 0%;
    left: 50%;
    z-index: 1;
    transform: translateX(-50%);
    border-radius: 24px;
  
    @media screen  and (max-width: 767px) {
      padding-top: 165px;
      padding-bottom: 40px;
      width: 280px;
    }
  
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      padding-top: 156px;
      padding-bottom: 40px;
      width: 220px;
      height: 485px;
    }
  
    @media screen and (min-width: 1280px) {
      padding-top: 170px;
      padding-bottom: 50px;
      width: 330px;
      max-height: 525px;
    }
  }
  
  .products__icecream_background {
    background-color: var(--background-pink);
  }
  
  .products__icecoffee_background {
    background-color: var( --cafe-bg-color);
  }
  
  .products__milkshakes_background {
    background-color: var(--food-truck-bg-color);
  }
  
